@media only screen and (max-width: 300px) {
  body {
    display: none;
  }
}

::-webkit-scrollbar {
  border: 0;
  background-color: #f6f6f6;
  width: 5px;
  height: 5px;
}
::-webkit-scrollbar-thumb {
  border-radius: 0;
  background-image: linear-gradient(to bottom, #FF8300 , #FF5C00);
  transition: all 0.5s;
}


@font-face {
  font-family: Roboto-Medium;
  src: url("../../resources/fonts/Roboto/Roboto-Medium.ttf");
}

@font-face {
  font-family: Gilroy-Regular;
  src: url("../../resources/fonts/Gilroy/GILROY-REGULAR.TTF");
}

@font-face {
  font-family: Gilroy-Bold;
  src: url("../../resources/fonts/Gilroy/GILROY-BOLD.TTF");
}

body {
  background-color: #ffffff;
  font-family: 'Gilroy-Regular', sans-serif!important;
  font-style: normal;
  font-size: 16px;
  color: #313949!important;
  outline: none!important;
  overflow: hidden;
}

a {
  text-decoration: none !important;
}
.registration {
  padding: 0 !important;
  margin: 0 !important;
  width: 100%;
  height: 100vh;
  background: url("../../resources/images/warehouse.jpg");
  background-size: 100% 100%;
}
.reg-input{
  margin-bottom: 30px;
  height: 50px;

}

.reg-pass{
  position: relative;
}

.reg-eye {
  position: absolute;
  left: 80%;
  top: 32px;
  outline: none!important;
  box-shadow: none!important;
}

.fl-l {
  height: 100vh;
  float: left;
}
.reg-form {
  margin-top: 22vh;
  border-radius: 10px;
  background: #FFFFFF;
  box-shadow: 0 0 5px #FFFFFF;
}
